import React from 'react';
import { Link } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
const Navbar = (props) => {

    const user = null;
    return (
        <nav className="navbar navbar-expand navbar-dark">
            <div className="container-fluid">
                        {user ? 
                        (<ul className="navbar-nav ms-auto me-4 mb-2 mb-lg-0">
                            <li className="nav-item">
                                <a className="nav-link">{user.name}</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link" href="#">Logout</a>
                            </li> 
                        </ul>) : 
                        (<ul className="navbar-nav ms-auto me-4 mb-2 mb-lg-0">
                            <li className="nav-item">
                                <Link className = "nav-link" style={{ fontSize: 'inherit', fontFamily: 'inherit', textDecoration: 'inherit', color: 'inherit'}} to="/react-hero/login">Login </Link>
                            </li>
                            <li className="nav-item">
                            <Link className = "nav-link" style={{ fontSize: 'inherit', fontFamily: 'inherit', textDecoration: 'inherit', color: 'inherit'}} to="/react-hero/create">Sign Up</Link>
                            </li> 
                        </ul>) }; 
                </div>
        </nav>
    );    
}

export default Navbar;