import React from 'react';
import Home from './components/home';
import Play from './components/Play/play';
import Controls from './components/Controls/controls';
import Leaderboard from './components/leaderboard';
import Callibrate from './components/Callibrate/callibrate';
import Login from './components/Login/login';
import Create from './components/Create/create';
import { BrowserRouter, Switch, Route } from 'react-router-dom';





const App = () => {
    /*const dispatch = useDispatch();
    useEffect(
        ()=> {dispatch(getPosts())},
         [dispatch]);*/
    return (<BrowserRouter>
                <Switch>
                    <Route path = "/react-hero" exact component = {Home} />
                    <Route path = "/react-hero/play" exact component = {Play} />
                    <Route path = "/react-hero/controls" exact component = {Controls} />
                    <Route path = "/react-hero/leaderboard" exact component = {Leaderboard} />
                    <Route path = "/react-hero/callibrate" exact component = {Callibrate} />
                    <Route path = "/react-hero/login" exact component = {Login} />
                    <Route path = "/react-hero/create" exact component = {Create} />
                </Switch>
            </BrowserRouter>);
}

export default App;