import React, { Component } from 'react';
import Navbar from './navbar.js';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Link } from 'react-router-dom';


class Home extends Component{
    render(){    
            return(
                <div>
                <div style={{textAlign: 'center'}}>
                    <Navbar />
                    <h1 style={{marginTop: '24px'}}>REACT HERO</h1>
                    <button style={{ marginTop: '4vw'}}><Link  style={{fontSize: 'inherit', fontFamily: 'inherit', textDecoration: 'inherit', color: 'inherit'}} to="/react-hero/play">Start Game</Link></button>
                    <button style={{ marginTop: 2+'vw'}}><Link style={{fontSize: 'inherit', fontFamily: 'inherit', textDecoration: 'inherit', color: 'inherit'}} to="/react-hero/controls">Controls</Link> </button>
                    <button style={{ marginTop: 2+'vw'}}><Link style={{fontSize: 'inherit', fontFamily: 'inherit', textDecoration: 'inherit', color: 'inherit'}} to="/react-hero/callibrate">Callibrate</Link> </button>
                    <button style={{ marginTop: 2+'vw'}}><Link style={{fontSize: 'inherit', fontFamily: 'inherit', textDecoration: 'inherit', color: 'inherit'}} to="/react-hero/leaderboard">Leaderboard</Link> </button>
                </div>
                </div>

            );
        }

}






export default Home;