import React, { Component } from 'react';
import { Link } from 'react-router-dom';

class Create extends Component{
    handleChange(){

    }
    submitScore(){

    }
    render(){
        return (
        <div style={{textAlign: 'center'}}>
            <h1>Login</h1>
            <br/>
            <form style={{ textAlign: 'right'}}onSubmit = {this.submitScore.bind(this)}>
            <div style={{paddingRight: 'calc(50% - 150px)'}}>
            <label style={{marginRight: '2vw'}}><h3>Username:</h3></label>
            <input 
                required
                type="text" 
                id="fname" 
                name="fname"  
                onChange = {this.handleChange.bind(this)}
            />
            <br/>
            <label style={{marginRight: '2vw'}}><h3>Email: </h3></label>
            <input 
                required
                type="email" 
                id="email" 
                name="email"  
                onChange = {this.handleChange.bind(this)}
            />
            <br/>
            <label style={{marginRight: '2vw'}}><h3>Password:</h3></label>
            <input 
                required
                type="password" 
                id="password" 
                name="password"  
                onChange = {this.handleChange.bind(this)}
            />
            <br/>
            <label style={{marginRight: '2vw'}}><h3>Confirm Password:</h3></label>
            <input 
                required
                type="password" 
                id="confirm-password" 
                name="confirm-password"  
                onChange = {this.handleChange.bind(this)}
            />
            <br/>
            </div>
            <div style={{textAlign:'center'}}> <input type = "submit" value="Submit"/>  </div>
            
        </form>
            <br/>
            <h5>
                <Link className = "nav-link" style={{ fontSize: 'inherit', fontFamily: 'inherit', textDecoration: 'inherit', color: 'inherit'}} to="/react-hero/login">Already have an account? Login here!</Link>
            </h5>
        </div>
        );
    }
}

export default Create;