import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { GoogleLogin } from 'react-google-login';
import { useDispatch } from 'react-redux';

const Login = () => {

    const dispatch = useDispatch();


    const handleChange = () => {

    }
    const submitScore = () => {

    }
    const googleFailure =  (error) => {
        console.log("failure");
        console.log(error);
    }
    const googleSuccess = async (res) => {
        // ?. means that if res doesnt exist, accessing profileObj of it wont throw an error
        const result = res?.profileObj;
        const token = res?.tokenId;

        try {
            dispatch({type: 'AUTH', data: {result, token} });
        } catch (error) {
            console.log(error);
        }
        
    }

        return (
        <div style={{textAlign: 'center'}}>
            <h1>Login</h1>
            <br/>
                <GoogleLogin 
                    clientId="585414485062-c0ivjqpj0o4hm95l8it2nbqomvioi5j4.apps.googleusercontent.com"
                    render={(renderProps)=>(
                    <button 
                        onClick={renderProps.onClick} 
                        disabled={renderProps.disabled}
                    >Login With Google</button>
                    )}
                    onFailure={googleFailure}
                    onSuccess={googleSuccess}
                    cookiePolicy="single_host_origin"
                />
            <br />
            <form onSubmit = {submitScore}>
            <label style={{marginRight: '2vw'}}><h3>Username:</h3></label>
            <input
                required 
                type="text" 
                id="fname" 
                name="fname"  
                onChange = {handleChange}
            />
            <br/>
            <label style={{marginRight: '2vw'}}><h3>Password:</h3></label>
            <input 
                required
                type="password" 
                id="password" 
                name="password"  
                onChange = {handleChange}
            />
            <br/>
            <input type = "submit" value="Submit"/>   
        </form>
            <br/>
            <h5>
                <Link className = "nav-link" style={{ fontSize: 'inherit', fontFamily: 'inherit', textDecoration: 'inherit', color: 'inherit'}} to="/react-hero/create">Don't have an account? Create one here!</Link>
            </h5>
        </div>
        );
}

export default Login;